.Spinner_loader__3RwcI,
.Spinner_loader__3RwcI:before,
.Spinner_loader__3RwcI:after {
    background: #00C7FD;
    animation: Spinner_load1__2fnOS 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
}
.Spinner_loader__3RwcI {
    color: #00C7FD;
    text-indent: -9999em;
    margin: 88px auto;
    position: relative;
    font-size: 11px;
    transform: translateZ(0);
    animation-delay: -0.16s;
}
.Spinner_loader__3RwcI:before,
.Spinner_loader__3RwcI:after {
    position: absolute;
    top: 0;
    content: '';
}
.Spinner_loader__3RwcI:before {
    left: -1.5em;
    animation-delay: -0.32s;
}
.Spinner_loader__3RwcI:after {
    left: 1.5em;
}
@keyframes Spinner_load1__2fnOS {
    0%,
    80%,
    100% {
        box-shadow: 0 0;
        height: 4em;
    }
    40% {
        box-shadow: 0 -2em;
        height: 5em;
    }
}

