    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: column;
    align-items: center;
    /*float: right;*/
    height: 100%;
}

@media (min-width: 500px) {
    .NavigationItems {
        flex-flow: row;
        align-items: center;
    }
}